@import "../utils/mixins";
@import "../utils/variables";

.article {
  @include grid;

  padding: 0 20px;

  @include for-tablet() {
    @include grid-tablet;
    padding: 0 72px;
  }

  @include for-desktop() {
    @include grid-desktop;
    padding: 0 120px;
  }

  grid-template-rows: auto;

  &__h2 {
    color: $hover-color;
    font-size: 32px;
    font-family: Manrope;
    font-style: normal;
    font-weight: $font-bold;
    line-height: 32px;

    grid-column: 1 / -1;
    margin: 48px 0 24px;

    @include for-tablet() {
      grid-column: 1 / 4;
      font-size: 40px;
      line-height: 100%;
      margin-top: 120px;
    }

    @include for-desktop() {
      grid-column: 1 / 7;
      font-size: 48px;
      line-height: 48px;
      margin-top: 120px;
    }
  }

  &__text {
    color: $hover-color;
    font-size: 16px;
    font-family: Manrope;
    font-style: normal;
    font-weight: $font-light;
    line-height: 140%;
    letter-spacing: 0.15px;
    height: fit-content;
    grid-column: 1 / -1;
    margin-bottom: 48px;

    @include for-tablet() {
      grid-column: 4 / 7;
      margin: 120px 0 120px;
    }

    @include for-desktop() {

      grid-column: 8 / 13;
    }
  }
}
