@import "../utils/mixins";
@import "../utils/variables";

.main-page {
  @include grid;
  background-color: $address-label-color;
  background-image: url('../img/header_small.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;

  @include for-tablet() {
    @include grid-tablet;
    background-image: url('../img/header_744.jpg');
   }

   @include for-desktop() {
    @include grid-desktop();
    background-image: url('../img/header_large.jpg');
   }

  &__header {
    grid-area: 1 / 1 / 2 / -1;
  }

  &__title {
    color: $background-color;
    font-size: 36px;
    font-family: Manrope;
    font-style: normal;
    font-weight: $font-bold;
    line-height: 100%;

    @include for-tablet() {
      font-size: 48px;
    }

    @include for-desktop() {
      font-size: 56px;
    }

    grid-area: 2 / 1 / 3 / -1;
    align-self: end;
  }
}
